// fileWorker.js

// Listen for messages from the main thread
self.addEventListener('message', event => {
  const { file } = event.data

  // Read the file contents
  const reader = new FileReader()

  reader.onload = function (e) {
    const fileContent = e.target.result

    // Post the file name and body to the specified endpoint
    postToEndpoint(file.name, fileContent, endpoint)
  }

  reader.readAsText(file)
})

// Function to post file name and body to the endpoint
function postToEndpoint(fileName, fileContent, endpoint) {

  // Make a POST request to the endpoint
  fetch(endpoint, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      fileName: fileName,
      fileContent: fileContent,
    }),
  })
    .then(response => response.json())
    .then(data => {
      // Send the response back to the main thread
      self.postMessage({ response: data })
    })
    .catch(error => {
      console.error('Error:', error)
      // Send the error back to the main thread
      self.postMessage({ error: error.message })
    })
}
